
import { AsYouType, parsePhoneNumber } from 'libphonenumber-js'
import { mapState } from 'vuex'

export default {
  data () {
    return {
      share: false,
      phone: '',
      amount: null,
      invalid: {
        phone: null,
        amount: null
      },
      sharing: false,
      phoneLocked: null
    }
  },
  computed: {
    ...mapState(['creds', 'wallet'])
  },
  watch: {
    share (d) {
      this.$nuxt.$emit('toggle_menu', d, 'share_credits')
    },
    phone (p) {
      if (this.phone_locked) { return }
      this.phoneLocked = true
      setTimeout(() => (this.phoneLocked = false), 10)
      this.phone = new AsYouType().input(p)
    }
  },
  mounted () {
    this.$nuxt.$on('share_credits', (open) => {
      if (!open) {
        return (this.share = false)
      }
      this.share = true
    })
  },
  methods: {
    isValid (phone) {
      try {
        const phoneNumber = parsePhoneNumber(phone, 'ZW')
        if (phoneNumber.isValid()) {
          return phoneNumber.number
        }
      } catch (error) {}
      return false
    },
    async shareCredit () {
      const phone = this.isValid(this.phone)
      if (!phone) {
        return (this.invalid.phone = true)
      }
      if (isNaN(this.amount) || this.amount <= 0) {
        return (this.invalid.amount = true)
      }

      // Firebase auth
      // Set Bearer
      this.sharing = true
      try {
        const { data: { data } } = await this.$axios.post('v0.1/share-credit', { amount: this.amount, phone })
        this.share = false
        this.$nuxt.$emit('topAlert', { msg: data.description, role: 'success' })
      } catch (error) {
        this.share = false
        const msg = error.response?.data?.error.message
        this.$nuxt.$emit('topAlert', { msg: msg || 'Something went wrong, try again', role: 'error' })
      }
      this.sharing = false
    }
  }
}
