
import { mapState } from 'vuex'

export default {
  middleware: 'server-auth',
  data () {
    return {
      tawkReady: false
    }
  },
  head () {
    return {
      title: `📞 Jambo Talk - Services (${this.creds.username})`
    }
  },
  computed: mapState(['creds']),
  mounted () {
    this.$tawkMessenger.$on('load', () => {
      this.tawkReady = true
    })
    this.$tawkMessenger.$on('chatMaximized', () => {
      this.tawkReady = true
    })

    try {
      if (this.$tawkMessenger.getStatus()) {
        this.tawkReady = true
      }
    } catch (error) {}
  },
  methods: {
    openChat () {
      this.tawkReady = false
      try {
        if (!this.$tawkMessenger.getStatus()) {
          return console.log('Loading')
        }
        this.$tawkMessenger.toggle()
      } catch (error) {
        console.error('Error loading chat', error)
      }
    }
  }
}
